*{margin:0;padding:0;box-sizing:border-box}body{position:relative;min-width:385px;width:100%;height:100%;background:#fff;color:#1d1d1d;font-family:"Roboto Condensed",sans-serif;font-size:16px}#root{height:100%}.wrapper{max-width:1280px;width:100%;margin:0 auto;padding:0 20px}.title{margin-bottom:20px;font-size:22px;font-weight:700}a{color:#1d1d1d;text-decoration:none}a:hover{color:#4e5685;opacity:.8}.mb_10{margin-bottom:10px}
.App{height:100%}
.Header{padding:5px 0;background-color:#eee}.Header__wrap{display:flex;justify-content:space-between;align-items:center}
.Logo{display:flex;align-items:center}.Logo__link{display:block;position:relative;left:-12px;width:100px;height:85px}.Logo__link svg,.Logo__link img{display:block;width:100%;height:100%}.Logo__desc{font-size:20px}
.form__input{display:block;width:100%;padding:5px 10px 5px 0;border:0;border-bottom:1px solid #c1c1c1;background-color:transparent;font-size:16px;font-family:"Roboto Condensed",sans-serif}.form__input:focus,.form__input:focus-visible,.form__select:focus,.form__select:focus-visible,.input__radio:focus,.input__radio:focus-visible,.form__textarea:focus,.form__textarea:focus-visible{outline:0;border-color:#2fbbf4}
.SearchForm{display:flex}.SearchForm .form__input{padding-left:0}.SearchForm__btn-wrap{position:relative;width:35px;height:35px}.SearchForm__btn-wrap input{display:block;position:absolute;top:0;left:0;width:100%;height:100%;opacity:0;cursor:pointer}.SearchForm__btn-wrap svg{display:block;width:100%;height:100%;cursor:pointer}
.Nav__link:not(:last-of-type){margin-right:20px}
.Footer{padding:20px 0 10px;background-color:#eee}.Footer__wrap{display:flex;justify-content:space-between;align-items:center}
.Social__wrap{display:flex}.Social__link{display:block;width:50px;height:50px;margin:0 5px}
.Copyright{max-width:180px;font-size:13px;color:#c1c1c1}.Copyright a{color:#c1c1c1}
.Btn{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px 10px;border:0;border-radius:3px;background-color:#2fbbf4;color:#1d1d1d;font-size:18px;cursor:pointer}.Btn:hover{background-color:#2ff489;opacity:1;opacity:initial}.Btn:focus{outline:none}

.Word__list{margin:40px 0 60px}.Word__item{display:grid;grid-template-columns:1fr 250px;padding:10px}.Word__item:nth-of-type(odd){background-color:#eee}.Word__wrap{display:grid;grid-template-columns:repeat(3, 1fr);align-items:center}.Word__input-wrap{position:relative;margin-right:20px}.Word__input-wrap input{display:none;position:absolute;left:0;top:-5px;z-index:2;padding:5px;border:1px solid #eee;background-color:#fff}.Word__input-wrap span{display:block}.Word__edit{display:flex;align-items:center;justify-content:flex-end}
.Btn_edit{display:flex;align-items:center;margin:0 5px;font-size:14px}.Btn_edit svg{width:20px;height:20px;margin-left:3px}.Btn_edit svg path{fill:#1d1d1d !important}
